@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");
.all-players-container {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding: 20px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  overflow-y: hidden;
}

.player-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}

.player-name {
  font-family: 'Roboto Slab', serif;
  font-size: 1.4em;
  font-weight: bolder;
  color: white;
  position: absolute;
  top: 5%;
}

.player-avatar {
  width: 144px;
  height: 144px;
  border-radius: 100%;
  border: 0.5rem inset #dd00ff;
  -webkit-box-shadow: 0 0 25px #ff8000;
          box-shadow: 0 0 25px #ff8000;
  background-image: url(../../assets/cat.gif);
  background-size: cover;
  background-position-x: 40%;
}

.player-container:hover > .player-avatar {
  border: 0.5rem inset #ff1f01;
}

.player-card-count {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  margin-top: -20%;
  font-family: 'Roboto Slab', serif;
  font-size: 1.4em;
  font-weight: bolder;
  color: white;
}

.player-card-count img {
  width: 3rem;
  height: 3rem;
}

.player-card-count p {
  margin: 0;
}

.slam {
  -webkit-animation: slam 1s;
          animation: slam 1s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  color: #C41818;
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 13em;
  font-weight: 900;
  position: relative;
  text-align: center;
  text-transform: uppercase;
}
