@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');


.all-players-container {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;

    padding: 20px;

    justify-content: space-between;

    overflow-y: hidden;

}

.player-container {
    width: fit-content;
    height: fit-content;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: relative;

}

.player-name {
    font-family: 'Roboto Slab', serif;
    font-size: 1.4em;
    font-weight:bolder;
    color: white;

    position: absolute;
    top:5%;
}

.player-avatar {
    width: 144px;
    height: 144px;
    border-radius: 100%;
    border: 0.5rem inset rgb(221, 0, 255);

    box-shadow: 0 0 25px rgb(255, 128, 0);

    background-image: url(../../assets/cat.gif);
    background-size: cover;
    background-position-x: 40%;


}

.player-container:hover > .player-avatar{
    border: 0.5rem inset rgb(255, 31, 1);
}

.player-card-count {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
    position: relative;
    margin-top: -20%;


    font-family: 'Roboto Slab', serif;
    font-size: 1.4em;
    font-weight:bolder;
    color: rgb(255, 255, 255);
    img {
        width: 3rem;
        height: 3rem;
    }
    p {
        margin: 0
    }
}


.slam
{
  animation: slam 1s;
  animation-timing-function: ease-in;
  color: #C41818;
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 13em;
  font-weight: 900;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  
}

