.game-container {
    width: 100%;
    height: 100vh;
    
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    background-color: rgb(28, 28, 28);
    background-image: url('../../assets/exploding-background.svg');
    background-repeat: no-repeat;
    background-position: center;
    // background-image: url('../../assets/woodTexture.jpg');
    background-size: cover;
    overflow: hidden;
}

.players-field {
  margin: 0 auto;
  width: 90%;

  display: flex;
  justify-content: center;
}


.game-field {
    position: relative;
    width: 45vw;
    height: 60vh;
    // background-image: url('../../assets/woodTexture.jpg');
    // background-size: cover;
    
    animation: rattle 0.2s;
    animation-delay: 1s;
    
}
.my-hand {
    display: flex; 
    position: absolute;
    justify-content:center;
    height: 30vh;
    bottom: 0.5vh;
}



.slam
{
  animation: slam 1s;
  animation-timing-function: ease-in;
  color: #C41818;
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 13em;
  font-weight: 900;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  
}

@keyframes slam
{
  0% {
    transform: scale(10, 10);
    opacity: 0;
  }
  
  40% {
    opacity: 0;
  }
  
  100%{
    transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes rattle
{
  0% { margin-top: 0; margin-left: 0; }
  10% { margin-top: -20px; margin-left: 0; }
  20% { margin-top: 0; margin-left: -20px; }
  30% { margin-top: 20px; margin-left: 0; }
  40% { margin-top: 0; margin-left: 20px; }
  50% { margin-top: -40px; margin-left: 0; }
  60% { margin-top: 0; margin-left: -40px; }
  70% { margin-top: 40px; margin-left: 0; }
  80% { margin-top: 0; margin-left: 40px; }
  90% { margin-top: -20px; margin-left: 0; }
  100% { margin-top: 0; margin-left: 0; }
}